import React from 'react';
import { Link } from 'react-router-dom';
import './Header.css';

function Header() {
  return (
    <header className="header">
      <div className="name">
        <a href="http://mateuszbelka.github.io/">
          <h1>Mateusz Belka</h1>
          <h2>Software Engineer</h2>
        </a>
      </div>
      <nav>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/">About</Link>
        </li>
      </nav>
    </header>
  )
}

export default Header;
